.candidate-container {
    width: 100%;
    background-color: var(--light-blue);
    display: flex;
    flex-direction: column;
    padding-bottom: 4em;
}

.candidate {
    max-width: 70%;
    margin: 0 auto;
    background-color: rgba(255, 255, 255, 0.986);
    border-radius: 5px;
    height: 100%;
    padding: 2em;
    display: flex;
    gap: 4em;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.candidate-container h3 {
    align-self: center;
    font-size: 2rem;
    padding: 2.5em 0em;
    color: #FFF;
}

.candidate__text-container {
    color: var(--very-dark-blue);
    font-size: 1.2rem;
    display: flex;
    flex-direction: column;
    max-width: 25ch;
    justify-content: space-between;
    padding-bottom: 4em
}

.candidate-form {
    display: flex;
    flex-direction: column;
    gap: 1.5em;
}

.file-input__container {
    display: flex;
    justify-content: flex-start;
    gap: 2em;
    padding-left: 1em;
}


#file-input {
    display: none;
}

.file-input-label,
.candidate__submit-button {
    cursor: pointer;
    background-color: var(--dark-blue);
    color: white;
    padding: 0.5em 1em;
    text-align: center;
    align-self: center;
    font-size: 1.2rem;
    transition: 0.2s;
    border-radius: 5px;
}

.candidate__submit-button {
    align-self: flex-end;
    font-size: 1.4rem;
    border: none;
}

.file-input-label:hover,
.candidate__submit-button:hover {
    background-color: var(--light-blue);
    color: white;
    transition: 0.2s;
}

.file-input__text {
    color: var(--very-dark-blue);
    font-size: 1.2rem;
    align-self: center;
}

.candidate__input {
    border: none;
    padding: 1em;
    font-size: 1.2rem;
    border: 1px solid var(--light-blue);
    border-radius: 5px;
}

.candidate__textarea {
    padding: 1em;
    resize: none;
    border: 1px solid var(--light-blue);
    height: 300px;
    font-size: 1.2rem;
    border-radius: 5px;
}

.candidate__link:link {
    text-decoration: none;
    color: var(--dark-blue);
}

.candidate__link:visited {
    color: var(--dark-blue);
}

@media screen and (max-width:1151px) {
    .candidate {
        display: flex;
        flex-direction: column;
        gap: 0em;
        text-align: center;
        align-items: center;
        min-width: 50%;
    }

    .candidate__text-container {
        padding-bottom: 2em;
        gap: 1em;
        max-width: 55ch;

    }

    .candidate__submit-button {
        align-self: center;
    }

    .candidate-form {
        width: 90%;
    }
}

@media screen and (max-width:710px) {
    .candidate {
        min-width: 90%;
        padding: 1em;
    }

    .candidate__text-container {

        max-width: 40ch;
        padding-top: 1em
    }

    .candidate-form {
        align-items: center;
    }

    .candidate-form input,
    .file-input__container,
    .candidate-form textarea {
        width: 300px;
    }

    .file-input__container {
        padding: 0;
    }

}