.employer-container {
    height: 900px;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-bottom: 4em;
    background-color: var(--light-blue);
    position:relative;
}

.employer-container>h3 {
    align-self: center;
    font-size: 2rem;
    padding: 2.5em 0em;
    color: #FFF;
    font-weight: 600;
}

.employer {
    width: 70%;
    background-color: #FFF;
    margin: 0 auto;
    height: 100%;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    background: url('../../assets/employer.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: flex-end;
    overflow: hidden;
}

.employer__text-content {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    width: 35%;
    padding: 1em 1.5em;
}

.employer__text-content p {
    font-size: 1.3rem;
    text-align: justify;
}

.employer__button-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
}

.employer__text-content button {
    align-self: center;
    border: none;
    padding: 0.5em 1em;
    font-size: 1.3rem;
    background-color: var(--dark-blue);
    color: #fff;
    font-weight: 500;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    max-width: 70%;
}

.employer__text-content button:hover {
    background-color: var(--light-blue);
    transition: 0.3s;
}

@media screen and (max-width:1394px) {
    .employer {
        width: 90%;

    }
}

@media screen and (max-width:1136px) {
    .employer {
        flex-direction: column;
    }

    .employer__text-content {
        flex-direction: row;
        width: 100%;
        align-items: center;
        justify-content: space-around;
    }

    .employer__text-content p {
        max-width: 35ch
    }

    .employer__text-content button {
        align-self: flex-end;
    }

}

@media screen and (max-width:921px) {


    .employer__text-content {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 2em;
        padding: 2em 1em;
    }

    .employer__text-content p {
        max-width: 50ch
    }

    .employer__text-content button {
        align-self: center;

    }

}