.footer-container {
    width: 100%;
    background-color: var(--light-blue);
}

.footer {
    width: 70%;
    margin: 0 auto;
    height: 100%;
    padding: 1em;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.footer ul {
    display: flex;
    font-size: 1.3rem;
    color: white;
    justify-content: space-around;
    width: 100%;
}

.footer li {
    cursor: pointer;
}

.footer a {
    text-decoration: none;
    color: white
}

.footer a:hover {
    color: var(--very-dark-blue);
}

.copyright {
    font-size: 1.2rem;
    color: #FFF;
    padding-top: 1.7em;
}

@media screen and (max-width:997px) {
    .footer {
        width: 90%;
    }
}


@media screen and (max-width:735px) {
    .footer ul {
        flex-direction: column;
        align-items: center;
        text-align: center;
        gap: 1em;
    }
}