.hero-container {
    width: 100%;
    height: calc(100vh - 100px);
}

.hero {
    height: 100%;
    width: 70%;
    margin: 0 auto;
    display: flex;
    gap: 6em;
    flex-direction: column;
    justify-content: space-between;
}

.hero__title-container {
    padding: 4em 0em 0em 0em;
    line-height: 2.7rem;
    margin-bottom: 2em;
}

.hero__title-container h1 {
    font-size: 4rem;
    color: var(--dark-blue);
}

.hero__title-container h3 {
    color: var(--light-blue);
    padding-left: 1em;
    font-weight: 300;
}

.hero__card {
    align-self: center;
    height: 600px;
    width: 850px;
    margin-top: -5em;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    display: flex;
    justify-content: space-between;
    overflow: hidden;
}

.hero__card-text {
    height: 100%;
    width: 65%;
    font-size: 1.5rem;
    align-self: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 3em 1.5em 1.5em;
    color: var(--very-dark-blue);
}

.hero__card-image {
    width: 35%;
    background-size: 300px;
}



.slide-circles-container {
    display: flex;
    gap: 1.5em;
    width: 100%;
    justify-content: center;
    align-items: flex-end;
}

.slide-circle {
    height: 15px;
    width: 15px;
    border-radius: 50%;
    background-color: rgb(231, 231, 231);
    cursor: pointer;
}

.slide-circle__active {
    background-color: var(--light-blue);
}

.hero__button-container {
    align-self: center;
    padding-bottom: 3em;
    display: flex;
    gap: 2em;
}

.hero__button-container button {
    border: none;
    background-color: var(--dark-blue);
    color: #FFF;
    font-size: 1.5rem;
    padding: 0.5em 1em;
    cursor: pointer;
    transition: 0.3s;
    border-radius: 5px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.hero__button-container button:hover {
    transition: 0.3s;
    background-color: var(--light-blue);
}

@media screen and (max-width:916px) {
    .hero {
        width: 100%;
        padding: 0em 1em;
        gap: 0em;
    }

    .hero__card {
        flex-direction: column;
        width: unset;
        height: unset;
    }

    .hero__card-text {
        padding: 1em;
        width: 100%;
        gap: 2em;
    }

    .hero-container {

        height: 680px;
    }

    .hero__button-container {
        gap: 1em;
    }

}