.partners-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 4em;
}

.partners-section h3 {
  align-self: center;
  font-size: 2rem;
  padding-bottom: 2.5em;
  color: var(--dark-blue);
  text-align: center;
  margin:0em 0.2em
}

.partners__wrapper {
  height: 250px;
  position: relative;
  overflow: hidden;
}

.partners__marquee {
  position: absolute;
  top: 0px;
  left: 0px;
  overflow: hidden;
  white-space: nowrap;
  animation: parnters__marquee 60s linear infinite;
  display: flex;
  align-items: center;


}

.partners__marquee img {
  padding: 0em 1em;
  user-select: none;
  max-width: 200px;

}

@keyframes parnters__marquee {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(-50%, 0);
  }
}