header {
    background-color: white;
    width: 100%;
    z-index: 2;
    box-shadow: rgba(33, 35, 38, 0.1) 0px 10px 10px -10px;
}

nav {
    width: 70%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    z-index: 3;
}

nav a {
    color: var(--very-dark-blue);
    text-decoration: none;
}

.navbar__logo {
    height: 100px;
    cursor: pointer;
    user-select: none;
}

.navbar__links {
    display: flex;
    gap: 2em;
}

.navbar__links li {
    cursor: pointer;
    font-size: 1.3rem;
    color: var(--very-dark-blue);
    font-weight: 500;
}

.navbar__links li:hover {
    color: var(--dark-blue);
}

.hamburger__icon {
    display: none;
}

.hamburger__wrapper {
    height: calc(100% - 105px);
    width: 100%;
    bottom: 0;
    left: 0;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.623)
}

.hamburger__menu {
    background-color: #FFF;
    position: absolute;
    right: 0;
    z-index:2;
    padding: 2em;
    width: 50%;
    height: 85vh;
}

.hamburger__links {
    display: flex;
    flex-direction: column;
    gap: 3em;

}

.hamburger__links li {
    cursor: pointer;
    font-size: 1.8rem;
    transition: 0.3s;
    color: var(--very-dark-blue);
}

.hamburger__links li:hover {
    transition: 0.3s;
    color: var(--dark-blue);
}

@media screen and (max-width:1014px) {
    .navbar__links {
        display: none;
    }

    .hamburger__icon {
        display: block;
        font-size: 2.5rem;
        cursor: pointer;
        color: var(--dark-blue);
    }
}

@media screen and (max-width:572px) {
    .hamburger__menu {
        width: 90%;
    }
}