.methodology-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    min-height: calc(100vh - 215px)
}

.methodology {
    width: 70%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 2em;
    height: 100%;
    padding: 4em 0em;
    align-items: stretch;

}

.methodology__item {
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border-radius: 5px;

}


.methodology__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    transition: 0.3s;
    padding: 1em;
}

.methodology__title:hover {
    color: var(--dark-blue);
    transition: 0.3s;
}


.methodology__title-icon {
    font-size: 1.5rem;
    opacity: 0.6;

}

.methodology__item h4 {
    font-size: 1.5rem;
    font-weight: 500;
}

.methodology__title-active {
    color: var(--dark-blue);
}

.methodology__content {
    margin-top: 0.3em;
    display: flex;
    gap: 1em;
    flex-direction: column;
    padding: 1em;
}

.methodology__no-display {
    display: none;

}

.methodology__text {
    font-size: 1.1rem;
}

.methodology-title {
    margin-top: 2em;
    font-size: 2rem;
    color: var(--dark-blue);
}


@media screen and (max-width:50em) {
    .methodology {
        width: 90%;
    }
}