.jobs-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-bottom: 4em;
}

.jobs {
    display: flex;
    gap: 2em;
    flex-wrap: wrap;
    width: 70%;
    margin: 0 auto;
    justify-content: center;
}

.jobs a {
    text-decoration: none;
}

.jobs-container>h3 {
    align-self: center;
    font-size: 2rem;
    padding: 2.5em 0em;
    color: var(--dark-blue);
    font-weight: 600;
}

.job {
    padding: 1em;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 2em;
    background-color: var(--dark-blue);
    color: #FFF;
    cursor: pointer;
    border-radius: 5px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    transition: 0.3s;
    width:300px;
    min-height:180px
}

.job:hover {
    background-color: var(--light-blue);
    color: #FFF;
    transition: 0.3s;
}

.job__title {
    font-size: 1.5rem;
    text-transform: uppercase;
}



.job__infos {
    display: flex;
    justify-content: space-between;
    font-size: 1.2rem;
}

@media screen and (max-width:623px) {
    .jobs {
        width: 90%;

    }
}