.employer-modal-wrapper {
    height: 100%;
    position: absolute;
    z-index: 3;
    background-color: rgba(0, 0, 0, 0.39);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    top: 0;
    left: 0;
}


.employer-modal-container {
    z-index: 4;
    display: flex;
    align-items: center;
    position: absolute;
    justify-content: center;
    height: 100%;
    width: 100%
}

.employer-modal {
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding: 2em;
    max-width:95%;
    background-color: #FFF;
}

.employer-modal__input {
    border: none;
    padding: 1em;
    font-size: 1.2rem;
    border: 1px solid var(--light-blue);
    border-radius: 5px;
}

.employer-modal__textarea {
    padding: 1em;
    resize: none;
    border: 1px solid var(--light-blue);
    height: 300px;
    font-size: 1.2rem;
    border-radius: 5px;
}

.employer-modal-title{
    text-align: center;
    color: var(--dark-blue);
    font-size:1.6rem;
    max-width:20ch;
    margin-bottom:1em;
}
.employer-modal-text{
    max-width:35ch;
    text-align: justify;
    margin-bottom:1em;
}

.employer-modal-button{
    align-self: flex-end;
    background-color: var(--dark-blue);
    color: #FFF;
    border: none;
    border-radius: 5px;
    padding: 0.5em 1em;
    font-size: 1.3rem;
    transition: 0.3s;
    cursor: pointer;
}