.tips-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    min-height: 720px;
    padding: 4em 0em;

}

.tips-title {
    margin-bottom: 2em;
    font-size: 2rem;
    color: var(--dark-blue);
}

.tips {
    display: grid;
    gap: 2em;
    grid-template-columns: 1fr 1fr 1fr;
}

.tip {
    text-align: center;
    height: 100%;
    color: #FFF;
    font-weight: 500;
    font-size: 1.1rem;
    padding: 1em;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 20ch;
    background-color: var(--light-blue);
    border-radius: 5px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    cursor: pointer;
}

.tips a {
    text-decoration: none;
}

@media screen and (max-width:805px) {

    .tips {
        grid-template-columns: 1fr 1fr;

    }

}

@media screen and (max-width:550px) {

    .tips {
        grid-template-columns: 1fr;

    }

}