.contact-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}

.contact-container h3 {
    align-self: center;
    font-size: 2rem;
    padding: 1.5em 0em;
    color: var(--dark-blue);
}

.contact {
    max-width: 70%;
    margin: 0 auto;
    padding-bottom: 3em;

}

.contact__form {
    display: flex;
    flex-direction: column;
    gap: 2em;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    padding:2em;
    border-radius: 5px;
}


.contact__input {
    border: none;
    padding: 1em;
    font-size: 1.2rem;
    border: 1px solid var(--light-blue);
    border-radius: 5px;
}

.contact__textarea {
    padding: 1em;
    resize: none;
    border: 1px solid var(--light-blue);
    height: 300px;
    font-size: 1.2rem;
    border-radius: 5px;
}

.contact__form button {
    align-self: flex-end;
    background-color: var(--dark-blue);
    color: #FFF;
    border: none;
    border-radius: 5px;
    padding: 0.5em 1em;
    font-size: 1.3rem;
    transition: 0.3s;
    cursor: pointer;
}

.contact__form button:hover {
    transition: 0.3s;
    background-color: var(--light-blue);

}

@media screen and (max-width:400px){
    .contact{
        max-width:90%;
    }
}