.job-page-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding:4em 0em;
}

.job-page {
    background-color: var(--light-blue);
    max-width: 700px;
    margin: 0 auto;
    padding: 2em;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    border-radius: 5px;
}

.job-page h1 {
    color: #FFF;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 1.5em;
    font-size:2.1rem;
}

.job-page__subtitle {
    margin-top: 1.5em;
    margin-bottom: 1em;
    color: #FFF;
    font-size: 1.5rem
}

.job-page__text {
    margin-bottom: 1em;
    color: #FFF;
    font-size: 1.2rem;
    text-align: justify;
}

@media screen and (max-width:40em){
    .job-page{
        max-width: 90%;
    }
}