.testimonials-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 4em;
}

.testimonials-container h3 {
    align-self: center;
    font-size: 2rem;
    padding-bottom: 2.5em;
    color: var(--dark-blue);
}

.testimonials {
    width: 70%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    gap: 3em;
    justify-content: center;
    padding-bottom: 6em;
}

.testimonial {
    display: flex;
    flex-direction: column;
    gap: 2em;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    border-radius: 5px;
    overflow: hidden;
    justify-content: space-evenly;

}



.testimonial__image {
    max-width: 350px;
    max-height: 200px;
    padding: 1em;
}

.testimonial__text-content {
    max-width: 350px;
    padding: 1.2em;
    color: var(--very-dark-blue);
    align-self: flex-end;
}

.testimonial__text-content h4 {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 1em;
}

.testimonial__text-content p {
    text-align: left;
    font-size: 1.1rem;
   
}

.testimonial__text{
    margin-bottom:1em;
}

@media screen and (max-width:1394px) {
    .testimonials {
        width: 90%;
    }
}